/* eslint-disable no-alert, no-console */

const DAPP_DEV_ADDR = 'n1f47UT35WoXbUEWfmatGKhFsyCBw84nSq3';
// const DAPP_PROD_ADDR = 'n1f47UT35WoXbUEWfmatGKhFsyCBw84nSq3';

const DAPP_DEV_LOCATION = 'https://testnet.nebulas.io';
// const DAPP_PROD_LOCATION = 'ttps://mainnet.nebulas.io';

import NebPay from 'nebPay';
import nebulas from 'nebulas';
import cookieStorage from './lib/storage';

const todoStorage = {
  fetch(key) {
    const todos = JSON.parse(localStorage.getItem(`todo_${key}`) || '[]');
    todos.forEach((todo, index) => { todo.id = index });
    todoStorage.uid = todos.length;
    return todos;
  },
  save(key, todos) {
    localStorage.setItem(`todo_${key}`, JSON.stringify(todos));
  }
}

const filters = {
  all(todos) {
    return todos;
  },
  active(todos) {
    return todos.filter(todo => !todo.completed);
  },
  completed(todos) {
    return todos.filter(todo => todo.completed);
  }
}

export default {
  name: 'app',
  data() {
    return {
      userAddr: null,
      userNonce: 0,
      neb: null,
      nebPay: null,
      Account: null,
      uid: 0,
      todos: [],
      newTodo: '',
      editedTodo: null,
      visibility: 'all',
    }
  },
  directives: {
    'todo-focus'(el, binding) {
      if (binding.value) {
        el.focus();
      }
    }
  },
  watch: {
    todos: {
      handler(todos) {
        // todoStorage.save(this.userAddr, todos);
        this.dapp_write(this.userAddr, todos);
      },
      deep: true,
    }
  },
  computed: {
    filteredTodos() {
      return filters[this.visibility](this.todos);
    },
    remaining() {
      return filters.active(this.todos).length;
    },
    allDone: {
      get() {
        return this.remaining === 0
      },
      set(value) {
        this.todos.forEach(todo => {
          todo.completed = value
        })
      }
    }
  },
  beforeMount() {
    this.dapp_init();
    this.user_init();
    this.todo_init();
  },
  methods: {
    dapp_init() {
      this.Account = nebulas.Account;
      this.neb = new nebulas.Neb();
      this.nebPay = new NebPay();
      this.neb.setRequest(new nebulas.HttpRequest(DAPP_DEV_LOCATION));
    },
    user_init() {
      this.userAddr = cookieStorage.get('userAddr');
      if (!this.userAddr) {
        this.userAddr = this.Account.NewAccount().getAddressString();
        cookieStorage.set('userAddr', this.userAddr);
      }
      this.neb.api.getAccountState(this.userAddr)
        .then(state => {
          this.userNonce = state.nonce;
          console.warn('[account init]', state);
        });
    },
    todo_init() {
      // this.todos = todoStorage.fetch(this.userAddr);
      this.dapp_read(this.userAddr)
    },
    dapp_read(key) {
      const from = this.userAddr;
      const to = DAPP_DEV_ADDR;
      const value = '0';
      const nonce = this.userNonce + 1;
      const gas_price = '1000000'
      const gas_limit = '2000000'
      const contract = {
        function: 'get',
        args: JSON.stringify([key]),
      }
      this.neb.api.call(
        from,
        to,
        value,
        nonce,
        gas_price,
        gas_limit,
        contract
      ).then(resp => {
          console.log('[Dapp Read]', resp)
          if (!resp.result || resp.result === 'null') return;
          const todos = JSON.parse(resp.result || '[]');
          todos.forEach((todo, index) => {
            todo.id = index;
          });
          this.uid = todos.length;
          this.todos = todos;
          console.warn('[todo init]', this.todos);
        })
        .catch(err => {
          console.error('[Dapp Read]', err)
        });
    },
    dapp_write(key, val) {
      const to = DAPP_DEV_ADDR;
      const value = '0';
      const callFunction = 'set';
      const callArgs = JSON.stringify([key, val]);
      const callback = resp => {
        console.log('[Dapp Write]', JSON.stringify(resp))
      };
      const options = {
        listener: callback,
      }
      const serialNumber = this.nebPay.call(
        to,
        value,
        callFunction,
        callArgs,
        options
      );
      const intervalQuery = setInterval(() => {
        this.nebPay.queryPayInfo(serialNumber)
          .then(resp => {
            console.log('[Dapp Write]', resp)
            const respObj = JSON.parse(resp)
            if(respObj && respObj.code === 0){
              clearInterval(intervalQuery)
            }
          })
          .catch(err => {
            console.error('[Dapp Write]', err)
          });
      }, 5000);
    },
    addTodo() {
      const value = this.newTodo && this.newTodo.trim();
      if (!value) return;
      this.todos.push({
        id: this.uid++,
        title: value,
        completed: false,
      })
      this.newTodo = '';
    },
    removeTodo(todo) {
      this.todos.splice(this.todos.indexOf(todo), 1);
    },
    editTodo(todo) {
      this.beforeEditCache = todo.title;
      this.editedTodo = todo;
    },
    doneEdit(todo) {
      if (!this.editedTodo) return;
      this.editedTodo = null;
      todo.title = todo.title.trim();
      if (!todo.title) {
        this.removeTodo(todo);
      }
    },
    cancelEdit(todo) {
      this.editedTodo = null;
      todo.title = this.beforeEditCache;
    },
    removeCompleted() {
      this.todos = filters.active(this.todos);
    },
    filterTodo(type) {
      if (filters[type]) {
        this.visibility = type;
      } else {
        window.location.hash = '';
        this.visibility = 'all';
      }
    }
  },
};



